/**
 * Object to hold data regarding an associate.
 * @author Alex, Xavier
 */
export class Associate {
    id: number;
    firstName: string;
    lastName: string;
    msid: number;
    marketingStatus: string;
    clid: number;
    client: string;
    bid: number;
    curid: number;
    curriculumName: string;
    ecid: number;
    endClientName: string;
    batchName: string;
    batchId: string;
}



